import React, { useState } from 'react'

export default function TextForm(props) {
    const handleUpClick = ()=>{
        if(text.length === 0){
            props.showAlert('Please enter some value to change in upper case!','danger');
        }else{
            let newText=text.toUpperCase();
            setText(newText);
            props.showAlert('Converted to uppercase!','success');
        }
    }
    
    const handleLowClick = ()=>{
        if(text.length === 0){
            props.showAlert('Please enter some value to change in lower case!','danger');
        }else{
            let newText=text.toLowerCase();
            setText(newText);
            props.showAlert('Converted to lowercase!','success');
        }
    }
    
    const handleSlugClick = ()=>{
        if(text.length === 0){
            props.showAlert('Please enter some value to create slug!','danger');
        }else{
            let newText=text.toLowerCase().replace(/\s\s+/g, ' ').trim().split(' ').join('-');
            setText(newText);
            props.showAlert('Slug created successfully!','success');
        }
    }
    
    const handleTitleCase = ()=>{
        if(text.length === 0){
            props.showAlert('Please enter some value to change in title case!','danger');
        }else{
            let text1=text.toLowerCase().replace(/\s\s+/g, ' ').trim().split(' ');
            let newText = "";
            console.log('text1 ',text1);
            text1.forEach(element =>{
                newText += element[0].toUpperCase() + element.substr(1)+' ';
            });
            console.log(newText);
            setText(newText);
            props.showAlert('Title case applied successfully!','success');
        }
    }
    
    const handleCopyText = ()=>{
        if(text.length === 0){
            props.showAlert('Please enter some value to copy text!','danger');
        }else{
            navigator.clipboard.writeText(text);
            props.showAlert('Copied to clipboard!','success');
        }
    }
    
    const handleClear = ()=>{
        if(text.length === 0){
            props.showAlert('Please enter some value to see the reflection!','danger');
        }else{
            let newText='';
            setText(newText);
            props.showAlert('Text crear successfully!','success');
        }
    }

    const handleOnChange = (event)=>{
        //console.log('Changed');
        setText(event.target.value);
    }
    const [text, setText] = useState('');
    return (
        <>
            <div className="container" style={{color:props.mode==='dark'? 'white' : 'black'}}>
                <div className="mb-3">
                <h1>{props.heading}</h1>
                <textarea className="form-control" value={text} onChange={handleOnChange} style={{backgroundColor:props.mode==='dark'? props.color : 'white',color:props.mode==='dark'? 'white' : 'black'}} id="exampleFormControlTextarea1" rows="10"></textarea>
                </div>
                <button className="btn btn-primary mx-1" onClick={handleUpClick}>Convert To Uppercase</button>
                <button className="btn btn-primary mx-1 " onClick={handleLowClick}>Convert To Lowercase</button>
                <button className="btn btn-primary mx-1 " onClick={handleSlugClick}>Create Slug</button>
                <button className="btn btn-primary mx-1 " onClick={handleTitleCase}>Title Case</button>
                <button className="btn btn-primary mx-1 " onClick={handleCopyText}>Copt to Clipboard</button>
                <button className="btn btn-primary mx-1 " onClick={handleClear}>Clear</button>
            </div>
            <div className="container my-3" style={{color:props.mode==='dark'? 'white' : 'black'}}>
                <h2>Your text summary</h2>
                <p>{text.length === 0 ? 0 : text.replace(/(^\s*)|(\s*$)/gi,"").replace(/[ ]{2,}/gi," ").replace(/\n /,"\n").split(" ").length  } words and {text.length} characters</p>
                <p>{text.length === 0 ?  0 :  0.008 * text.replace(/(^\s*)|(\s*$)/gi,"").replace(/[ ]{2,}/gi," ").replace(/\n /,"\n").split(" ").length} Read time</p>
                <h2>Preview</h2>
                <p>{text.length > 0 ? text : 'Enter some code in above text area to preview here' }</p>
            </div>
        </>
    )
}
