import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';


export default function Navbar(props) {
    const sendThemeColor = (color) =>{
      props.toggleThemeMode(color);
    }
    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">{props.title}</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link active" aria-current="page" to="/">Home</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/about">{props.aboutText}</Link>
              </li>
            </ul>
            {/* <form className="d-flex">
              <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
              <button className="btn btn-primary" type="submit">Search</button>
            </form> */}
            <button type="button" className="btn btn-primary btn-sm mx-1" onClick={(event) => sendThemeColor("#0d6efd")}></button>
            <button type="button" className="btn btn-danger btn-sm mx-1" onClick={(event) => sendThemeColor("#dc3545")}></button>
            <button type="button" className="btn btn-warning btn-sm mx-1" onClick={(event) => sendThemeColor("#ffc107")}></button>
            <button type="button" className="btn btn-secondary btn-sm mx-1" onClick={(event) => sendThemeColor("#6c757d")}></button>
            <button type="button" className="btn btn-info btn-sm mx-1" onClick={(event) => sendThemeColor("#0dcaf0")}></button>
            
            <div className={`form-check form-switch text-${props.mode==='dark'? 'white' : 'black'}`}>
              {/* <input className="form-check-input" onClick={props.toggleMode} type="checkbox" role="switch" id="flexSwitchCheckDefault"/> */}
              {props.mode==='dark'? <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Disable Dark Mode <button type="button" className="btn btn-danger btn-sm mx-1" onClick={props.disableDarkMode}>X</button></label> : <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Enable Dark Mode</label>}
              
            </div>
          </div>
        </div>
      </nav>
    )
}

// Validation for proptypes
Navbar.propTypes={
    title : PropTypes.string.isRequired,
    aboutText : PropTypes.string.isRequired
}

// Default props
Navbar.defaultProps={
    title:"Title goes here",
    aboutText:"About"
}
