import './App.css';
import About from './components/About';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
import React, { useState } from 'react';
import Alert from './components/Alert';
import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";


function App() {
  const [mode, setMode] = useState('light');
  const [alert, setAlert] = useState(null);
  const [color, setColor] = useState('white');

  const showAlert = (message,type) =>{
    setAlert({
      message:message,
      type:type
    });
    setTimeout(() => {
      setAlert(null);
    }, 1500);
  }

  const disableDarkMode = () =>{
    setMode('light');
    document.body.style.backgroundColor = "white";
    showAlert('Dark mode disabled!','success');
    document.title = "TextUtils - Light Mode";
  }
  const toggleMode = () =>{
    if(mode === 'light'){
      setMode('dark');
      document.body.style.backgroundColor = "#456581";
      showAlert('Dark mode enabled!','success');
      
    }else{
      setMode('light');
      document.body.style.backgroundColor = "white";
      showAlert('Light mode enabled!','success');
    } 
  }

  const toggleThemeMode = (color) =>{
      setColor(color);
      setMode('dark');
      document.body.style.backgroundColor = color;
      showAlert('Dark mode enabled!','success');
      document.title = "TextUtils - Dark Mode";
  }
  return (
    <>
      <Router>
      {/* <Navbar title={"TextUtils"} aboutText="About Us"/> */}
      <Navbar title={"TextUtils"} mode={mode} disableDarkMode={disableDarkMode} toggleMode={toggleMode} toggleThemeMode={toggleThemeMode}/>
      <Alert alert={alert}/>
      <div className="container my-3">
      <Routes>
          <Route path="/about" element={<About color={color} mode={mode}/>}/>
          <Route path="/" element={<TextForm showAlert={showAlert} color={color} heading="Enter text below to analyse" mode={mode}/>}/>
      </Routes>
      </div>
      </Router>
    </>
  );
}

export default App;
